@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Cutive+Mono&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
:root {
  --primary-color: #c7b198;
  --background-color: #1b262c;
  --background-color-light: #5f676b;
  --color-1: #1b262c;

  --ship-color: #fa744f;
  --ship-hit-color: #313b41;
  --empty-cell: #fdcb9e;

  color: midnightblue;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* My modifications */
* {
  box-sizing: border-box;
  outline: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
  background: none;
  cursor: pointer;
}

/*
* Fonts used:
* - Material Icons
*/

#page-home {
  width: 100vw;
  height: 100vh;

  box-sizing: border-box;
  overflow-y: hidden;
  overflow-x: hidden;

  background-color: var(--background-color);
}

#page-home .content {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  margin-top: 50px;
}

header {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  padding-top: 20px;

  position: absolute;
  top: 0;
}

header .logo {
  display: flex;
  align-items: center;
  justify-content: center;

  padding-bottom: 15px;
  border-bottom: 3px solid var(--empty-cell);
}

header h1 {
  color: var(--empty-cell);
  font-family: "Cutive Mono", monospace;
  font-size: 36px;
  font-weight: bold;

  margin-left: 24px;
}

.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.game-container .game-header {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  margin-bottom: 30px;
}

.game-container .game-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;

  align-items: center;
  justify-items: center;

  width: 90%;
}

.game-container .game-footer {
  margin-top: 50px;
}

.game-container .game-footer button {
  color: var(--empty-cell);
  font-size: 30px;
  font-weight: bolder;
  font-family: "Cutive Mono", monospace;

  border: 3px dashed var(--ship-color);
  border-radius: 10px;
  padding: 4px 16px;

  transition: 200ms;
}

.game-container .game-footer button:hover {
  border: 3px solid var(--ship-color);
  background-color: var(--ship-color);
}

.board-container {
  position: relative;
}

.board-container .board-overlay {
  position: absolute;
  visibility: visible;

  width: 100%;
  height: 100%;
  margin: 20px;

  opacity: 0.8;
  background-color: var(--background-color);

  transition: visibility 0s, opacity 0.5s ease-out;
}

.board-container .board-overlay.hidden {
  visibility: hidden;
  opacity: 0;
}

.board-container .gameboard {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-gap: 2px;
  gap: 2px;

  padding: 20px;
  border-radius: 10px;

  background-color: var(--color-1);

  width: 400px;
  height: 400px;
}

.board-container .board-footer {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;

  position: absolute;
  bottom: -15px;
}

.board-container .board-footer h1 {
  color: var(--empty-cell);
  font-size: 20px;
  font-family: "Cutive Mono", monospace;
  font-weight: bold;
}

.cell {
  display: flex;
  align-items: center;
  justify-content: center;

  background-color: var(--empty-cell);
  cursor: pointer;
  transition: 300ms;
}

.cell:hover {
  opacity: 60%;
}

.cell.occupied.show {
  background-color: var(--ship-color);
}

.cell.occupied.attacked {
  background-color: var(--ship-hit-color);
}

.cell p {
  color: var(--background-color);
  font-family: "Material Icons";
  font-size: 10px;
}

.cell.occupied p {
  color: white;
  font-size: 20px;
  font-style: bold;
}

.harbour-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  height: 100%;
  padding: 20px 0;
}

.harbour-container .buttons {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 8px;
  gap: 8px;
}

.harbour-container .buttons button {
  color: var(--empty-cell);
  font-size: 16px;
  font-family: "Cutive Mono", monospace;
  font-weight: bold;

  border: 2px solid var(--empty-cell);
  border-radius: 8px;
}

.harbour-container .buttons button:hover {
  background-color: var(--empty-cell);
  color: var(--background-color);

  transition: 300ms;
}

.harbour-container .ships {
  display: flex;
}

.ship-component {
  cursor: pointer;
  transition: opacity 300ms;
}

.ship-component:hover {
  opacity: 0.6 !important;
}

.ship-component .cell {
  opacity: 1;
}

.scoreboard-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;
}

.scoreboard-container .scoreboard-ships {
  display: flex;
  flex-direction: column;

  transform: scale(0.7);
}

.scoreboard-container .scoreboard-ships .scoreboard-ship {
  display: flex;
  flex-direction: column;

  position: relative;
}

.scoreboard-container .scoreboard-ships .scoreboard-ship h1 {
  color: var(--empty-cell);
  font-size: 20px;
  font-family: "Cutive Mono", monospace;
  font-weight: bold;

  margin-bottom: 5px;
}

.scoreboard-container .scoreboard-ships .scoreboard-ship .ship-overlay {
  position: absolute;
  bottom: 15px;
  width: 100%;
  height: calc(100% - 40px);

  z-index: 10;
  background-color: var(--background-color);
  opacity: 0.8;

  transition: opacity 300ms;
}

.scoreboard-container .scoreboard-ships .scoreboard-ship .ship-overlay.hidden {
  opacity: 0;
}

.scroller {
  width: 500px;
  height: 100px;

  display: flex;
  flex-direction: column;

  padding: 8px;
  color: var(--empty-cell);
  font-family: monospace;
  line-height: 1.3em;

  border: 2px solid var(--ship-color);
  border-radius: 10px;

  overflow: auto;
}

/* width */
.scroller::-webkit-scrollbar {
  width: 5px;
}

/* Track */
.scroller::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
.scroller::-webkit-scrollbar-thumb {
  background: #fa744f;
  border-radius: 20px;
}

/* Handle on hover */
.scroller::-webkit-scrollbar-thumb:hover {
  background: #fdcb9e;
}

